.App {
  font-family: sans-serif;
  text-align: center;
}

.Banner {
  width: 100%;
  height: 450px;
  background-color: white;
}

.dog_pic {
  position: relative;
  width: 100%;
  height: 100%;
  filter: brightness(75%);
  z-index: 0;
}

.Shop {
  margin-top: 50px;
  width: 100%;
  height: 500px;
}

.products {
  width: 400px;
  padding: 0 50px;
}

.cartArea {
  width: 100%;
  height: 300px;
  background-color: #d7e9eb;
}

.end {
  width: 100%;
  height: 50px;
  background-color: #2a7886;
}

.rule {
  border: 10px dashed #2a7886;
  width: 75%;
  margin: auto;
  margin-top: 10%;
  margin-bottom: 5%;
}

.end_img {
  position: relative;
  width: 850px;
  height: 300px;
  float: left;
}

.contact-info {
  padding-top: 55px;
  padding-right: 55px;
}

#StartShopButton {
  background-color: #79bac1;
  border: none;
  position: absolute;
  top: 70%;
  left: 42%;
  color: white;
}

#navbar-header {
  background-color: #79bac1 !important;
}

h1 {
  font-family: "Lato:900", sans-serif;
  position: absolute;
  text-align: center;
  color: white;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

h2 {
  font-family: "Lato", sans-serif;
  position: relative;
  text-align: left;
  padding-top: 20px;
  padding-left: 20px;
}

h3 {
  padding-top: 20px;
  padding-right: 20px;
  text-align: right;
  color: #2a7886;
}

h4 {
  font-family: "Lato", sans-serif;
  position: absolute;
  color: white;
  left: 50%;
  top: 57%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

h5 {
  text-align: right;
  padding-right: 20px;
  text-align: right;
  color: #2a7886;
}

h6 {
  padding-top: 15px;
  color: white;
}

.product_pic {
  width: 50%;
  height: 250px;
  margin: 0 25%;
  padding-top: 25px;
}
